import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

import { registroPSV } from '../modelo_registroPSV/registroPSV';

//dato del calculo
import { datoV } from '../modelo_datoV/datoV';

@Component({
  selector: 'app-registro-pedi-semanal-v',
  templateUrl: './registro-pedi-semanal-v.page.html',
  styleUrls: ['./registro-pedi-semanal-v.page.scss'],
})
export class RegistroPediSemanalVPage implements OnInit {


   //dato de calculo
   objeto12:datoV = { semana:"", total_costo:""}
  
   pruebas12: datoV[] = [];
   url3 = 'http://localhost/proG/server_api/menu_datoV.php';
  

  
  objeto:registroPSV = { Des_de_semana:"", mes:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:""}
  
  pruebas: registroPSV[] = [];
  url = 'http://localhost/proG/server_api/crear_registroPSV.php';
  url2 = 'http://localhost/proG/server_api/menu_registroPSV.php';
 
  disabledButton;

  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders
  ) {
    this.listarRegistroPSV();
    this.listarRegistroDV();
   }

  ngOnInit() {
  }

  agregarRegistroPSV(obj :registroPSV): Observable<registroPSV>{
    return this.http.post<registroPSV>(this.url, JSON.stringify(obj))
  }

  async agregarSE() {
    this.agregarRegistroPSV(this.objeto)
    .subscribe((resp) =>{
      this.objeto.Des_de_semana='';
      this.objeto.mes='';
      this.objeto.lunes='';
      this.objeto.martes='';
      this.objeto.miercoles='';
      this.objeto.jueves='';
      this.objeto.viernes='';
      this.objeto.sabado='';
      this.objeto.domingo='';
      this.listarRegistroPSV();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }

  getRegistroPSV(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarRegistroPSV(){
    this.pruebas = [];
    this.getRegistroPSV().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas.push(res[i]);
      }
    })
  }

  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_PSV',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroPSV();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }

  abrirCrud(a){
    this.router.navigate(['/modsemanav/'+a]);
  }

   /// datos visualizzar 
   getRegistroDV(): Observable<any>{
    return this.http.get(this.url3);
  }

  listarRegistroDV(){
    this.pruebas12 = [];
    this.getRegistroDV().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.pruebas12.push(res[i]);
      }
    })
  }
  
  async delDatos(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_DV',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarRegistroDV();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

}
